import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import router from './path'

// 前置守卫
router.beforeEach((to, from ,next) => {
  
  window.document.title = to.meta.title

  // 在这可以判断用户是否登录

  next()
})

export default router


