import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    named: 'VcrTing',
    newName: 'Qiong',
  },
  mutations: {
    changeName(state, v) {
      state.named = v
    }
  },
  actions: {

  },
  // 过滤获取东西
  getters: {
    newName(state) {
      return v => {
        return state.newName + '_' + v
      }
    }
  },
  modules: {

  }
})

export default store


